.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.big-box {
  max-height: 450px;
  overflow: auto;
}

.info-box-text {
  text-align: center;
  border-bottom: 1px solid #efefef;
}

.info-box-number {
  margin: 15px 0 10px;
  text-align: center;
}

.grid-item,
.grid-item2 {
  width: 100%;
}

.form-control {
  color: #000 !important;
}

